<template>
  <div>
    <b-col>
      <b-card
        :img-src="url"
        :img-alt="id"
        img-top
        tag="article"
        style="max-width:20rem"
        class="mb-2"
        :footer="name"
        @click="showModal"
      >
        <b-card-text :title="url"> {{bio}}</b-card-text>
        <!-- <b-button variant="primary">Apply</b-button> -->
      </b-card>
    </b-col>

    <b-modal size="xl" ref="my-modal" hide-footer title="Board of Directors">
      <div class="d-block text-center">
       <b-card
        :img-alt="id"
        :footer="name">        
      </b-card>
      <p class="modal-p">{{bio}}</p>
      </div>
      <b-button class="mt-3" variant="outline-danger" block @click="hideModal">Close Me</b-button>
    </b-modal>

  </div>
</template>

<script>
export default {
  props: ["name", "bio", "id", "url"],

  methods: {
      showModal() {
        this.$refs['my-modal'].show()
      },
      hideModal() {
        this.$refs['my-modal'].hide()
      },
      toggleModal() {
        // We pass the ID of the button that we want to return focus to
        // when the modal has hidden
        this.$refs['my-modal'].toggle('#toggle-btn')
      }
    }
};


</script>

