<template>
  <b-container>
    <b-row aligh-v="center">
      <executive-member
        v-for="executive in executives"
        :key="executive.id"
        :name="executive.name"
        :bio="executive.bio"
        :position="executive.position"
        :url="executive.url"
      >
      </executive-member>
    </b-row>
  </b-container>
</template>

<script>
// @ is an alias to /src
import executivemember from "@/components/executive-member.vue";

export default {
  name: "Home",
  components: { "executive-member": executivemember },
  mounted() {
    this.fetchData();
  },
  data() {
    return {
      executives: [],
      currentPage: 1,
      rows: 1,
      perPage: 3
    };
  },

  methods: {
    async fetchData() {
      const response = await fetch("executive.json");
      const val = await response.json();
      this.executives = val;
      this.rows = this.executives.length;
    }
  }
};
</script>


